import React from 'react';
import TransitionLink from 'gatsby-plugin-transition-link';

const fadeTiming = 0.3;

const fadeLink = props => {
  return (
    <TransitionLink
      {...props} // children also passed here
      exit={{
        zIndex: 2,
        length: fadeTiming,
        trigger: ({ node }) => fadeOut(node),
      }}
      entry={{
        delay: fadeTiming,
        length: fadeTiming,
        trigger: ({ node }) => fadeIn(node),
      }}
    />
  );
};

export default fadeLink;

const fadeOut = node => {
  node.querySelector('section').classList.add('-page-fade-out');
};

const fadeIn = node => {
  node.querySelector('section').classList.add('-page-fade-in');
};
